import {
    IonCard,
    IonCardContent,
    IonCardHeader,
    IonItem, 
    IonButton,
    IonCardTitle,
  } from "@ionic/react";
import React, { ReactElement,  } from "react";
  export type CardProps = {
    number: number,
    title: string;
    content: string;
    handleDelete: () => void;
    handleModal: () => void;
  };
  
  export default function Card({
    number,
    title,
    content,
    handleDelete,
    handleModal
  }: CardProps): ReactElement {
    return (
      <IonCard>
        <IonCardHeader>
          <IonCardTitle>{number + 1}.</IonCardTitle>
        </IonCardHeader>
        <IonCardContent>Title: {title}</IonCardContent>
        <IonCardContent>Description: {content}</IonCardContent>
        <IonCard>
          <IonItem className="ion-activated">
            <IonButton onClick={handleDelete} color="danger" shape="round">Delete</IonButton>
          </IonItem>

          <IonItem className="ion-activated">
            <IonButton onClick={handleModal} color="light" shape="round">&nbsp;&nbsp;Edit&nbsp;&nbsp;</IonButton>
          </IonItem>
        </IonCard>

      </IonCard>
    );
  }