import React, { useState, useEffect } from 'react';
import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonInput, IonModal, IonCard, IonItem, IonButton } from '@ionic/react';
import './Home.css';
import Card from "../components/Card";
type Todos = {
  title: string;
  description: string;
  _id: string;
};
const Home: React.FC = () => {
  const [title, onChangeTitle] = useState<string>('')
  const [description, onChangeDesc] = useState<string>('')
  const [todoList, setTodoList] = useState<any[]>([])
  const [showModal, setShowModal] = useState<boolean>(false)
  const [updateTitle, setUpdateTile] = useState<string>('')
  const [updateDesc, setUpdateDesc] = useState<string>('')
  const [id, setId] = useState<string>('')

  const addTodo = () => {
    fetch('http://localhost:3000/todos', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ 
        title, 
        description
      })
    })
    .then(response => response.json())
    .then(result => setTodoList((prev: any) => [result, ...prev]))
  }

  useEffect(() => {
  }, [todoList]);

  const deleteTodo = (id: string, index: number) => {
    fetch('http://localhost:3000/todos/' + id, {
      method: 'DELETE',
    })
    .then(_res => window.location.reload())
  }

  useEffect(() => {
  }, [todoList]);

  useEffect(() => {
    fetch("http://localhost:3000/todos")
    .then(response => response.json())
    .then(todo => setTodoList(todo))
  },[])

  const handleClick = (id: string) => {
    fetch('http://localhost:3000/todos/' + id)
    .then(response => response.json())
    .then(result => {
      setShowModal(true)
      setUpdateTile(result.description)
      setId(result._id)
      setUpdateDesc(result.title)
    })
  }


  const submitUpdate = () => {
    setShowModal(false)
    fetch('http://localhost:3000/todos/' + id, {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ 
        title: updateTitle, 
        description: updateDesc
      })
    })
    .then(response => response.json())
    .then(_res => window.location.reload())
  }

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Todo APP</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Todo APP</IonTitle>
          </IonToolbar>
        </IonHeader>
          <IonContent fullscreen>
            <IonInput value={title} onIonChange={e => onChangeTitle(e.detail.value!)} placeholder="Enter Title" clearInput></IonInput>
            <IonInput value={description} onIonChange={e => onChangeDesc(e.detail.value!)} placeholder="Enter Description" clearInput></IonInput>
            <IonButton expand="full" onClick={() => addTodo()} >Add To DO</IonButton>
            <IonHeader collapse='condense'>
              <IonToolbar>
                <IonTitle size='large'>Todo</IonTitle>
              </IonToolbar>
            </IonHeader>
            <IonModal isOpen={showModal}>
              <IonCard>
                <IonItem className="ion-activated">
                  <IonInput value={updateTitle} onIonChange={e => setUpdateTile(e.detail.value!)} placeholder="Enter Title" clearInput></IonInput>
                </IonItem>

                <IonItem className="ion-activated">
                  <IonInput value={updateDesc} onIonChange={e => setUpdateDesc(e.detail.value!)} placeholder="Enter Description" clearInput></IonInput>
                </IonItem>
              </IonCard>
              <IonButton onClick={() => submitUpdate()}>Update</IonButton>
              <IonButton onClick={() => setShowModal(false)} color="light">Close Modal</IonButton>
            </IonModal>
              {todoList?.map((key: Todos, index: number) => (
                <Card
                  key={key._id}
                  number={index}
                  title={key.title}
                  content={key.description}
                  handleDelete={() => deleteTodo(key._id, index)}
                  handleModal={() => handleClick(key._id)}
                />
              ))}
        </IonContent>
      </IonContent>
    </IonPage>
  );
};

export default Home;
